<template>
  <div class="body__mask">
    <app-main-header
      :images="'/images/customers/main.png'"
      :caption="caption"
      :path="path"
    />

    <router-view 
      :data="dataList"
    />

    <app-footer />      
  </div>
</template>

<script>
import mixinProduct from '../product-mixin';

import '../assets/style/production.css';

export default {
  name: 'Production',
  components: {
    'app-main-header': () => import('../components/MainHeader.vue'),
    'app-footer': () => import('../components/Footer.vue'),
  },
  data() {
    return {
      caption: 'Наша продукция',
      path: [],

      dataList: {},
    };
  },
  mixins: [mixinProduct],
  created() {
    this.changeCaption();
  },
  methods: {
    changeCaption() {
      if (!this.$route.params)
        return;

      this.type = this.$route.params.type;
      this.list = this.$route.params.list;
      this.name = this.$route.params.name;

      if (this.name) {
        this.dataList = this.productionList[this.type]['childrens'][this.list]['childrens'][this.name];
        this.path = [
          {
            name: 'Наша продукция',
            path: '/production'
          },
          {
            name: this.productionList[this.type].name,
            path: '/production/' + this.type
          },
          {
            name: this.productionList[this.type]['childrens'][this.list].name,
            path: '/production/' + this.type + '/' + this.list
          },
        ];
      } else if (this.list) {
        this.dataList = this.productionList[this.type]['childrens'][this.list];
        this.path = [
          {
            name: 'Наша продукция',
            path: '/production'
          },
          {
            name: this.productionList[this.type].name,
            path: '/production/' + this.type
          },
        ];
      } else if (this.type) {
        this.dataList = this.productionList[this.type];
        this.path = [
          {
            name: 'Наша продукция',
            path: '/production'
          },
        ];
      } else {
        this.dataList = {}
      }

      this.caption = this.dataList.name || 'Наша продукция';
    },
    // getPath(data) {
    //   if (data) {
    //     let res = data.map((item) => {
    //       return `<a href="${item.path}">${item.name}</a>`;
    //     });

    //     return res;
    //   } else
    //     return '';
    // },
    toggleCildrenTree(e) {
      let elemSpan = e.target.closest('.toggle-tree');
      let elemLi = elemSpan.closest('li');
      
      elemSpan.classList.toggle('active');
      elemLi.classList.toggle('p-tree__block__li--active');
    },
  }
}
</script>

